.App {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    font-family: Arial, Helvetica, sans-serif;
  }
  
  body {
    margin: 0%;
    padding: 0%;
    background-color: rgb(30, 88, 188);
  }
  
  .header {
    flex: 30%;
    background-color: rgb(9, 19, 49);
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0px;
    border-radius: 10px;
  }
  .title{
    width: 100%;
   align-items: center;
   justify-content: center;
    flex: 10%;
    /* background-image: linear-gradient(rgb(201, 20, 105), rgb(53, 45, 50)); */
    display: flex;
    font-family: 'Caveat', cursive;
  }
  
  .header .inputContainer {
    display: flex;
    flex-direction: column;
  }
  
  .header input {
    width: 200px;
    height: 40px;
    border: none;
    border-bottom-left-radius: 8px;
    border-top-left-radius: 8px;
    padding-left: 10px;
    font-size: 17px;
    border: 1px solid grey;
  }
  
  .header button {
    width: 70px;
    height: 87px;
    border: none;
    border-bottom-right-radius: 8px;
    border-top-right-radius: 8px;
    padding-left: 10px;
    cursor: pointer;
    /* border-radius: 20px; */
    background: rgb(30, 88, 188);
  }
  
  .todoList {
    flex: 70%;
    width: 100%;
    display: flex;
    align-items: center;
    padding-top: 50px;
    flex-direction: column;
  }
  
  .task {
    width: 500px;
    height: 50px;
    display: flex;
    color: white;
    margin: 15px;
  }
  
  .task .content {
    flex: 80%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .task .content span {
    display: grid;
    place-items: center;
    border: 1px solid white;
    width: 100%;
    height: 100%;
    font-size: 18px;
    border-right: none;
    background-color: rgb(9, 19, 49);

  }
 
  
  .task button {
    flex: 20%;
    height: 100%;
    border: none;
    background-color: lightseagreen;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    color: white;
    cursor: pointer;
  }